import React, {useState} from 'react';
import Visualization from './Visualization'
import * as htmlToImage from 'html-to-image';
import { DefaultLayout } from '../layouts';
import { Container } from '@material-ui/core';
import MainFooter from './layout/MainFooter';
import { 
  Col, Row, } from 'shards-react';
var isLoad = false;
var isDownload = false;
const HtmlToImage = ({data, saveAs, }) =>{

  const exportAsPicture = () => {

    var downloadButton = document.getElementsByClassName('downloadBtn');

    var t0 = performance.now()

    var data = document.getElementsByClassName('printPNG');

    for (var i = 0; i < data.length; ++i){
        htmlToImage.toPng(data[i]).then((dataUrl) => {
          saveAs(dataUrl, 'online-teaching-report'+new Date()+'.png');
        });
    }

    var t1 = performance.now()

    var t = t1 - t0
  }

  const preLoad = (e) => {
    isLoad = e;
    console.log(e);
  }

  return (<div style={{ padding: "40px", minHeight: "100vh"}} >
          <DefaultLayout exportAsPicture={exportAsPicture}/>
            <Row>
            </Row>
          <h1></h1>
          <MainFooter></MainFooter>
  </div>
  )};

export default HtmlToImage;